import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { Client } from '../../models/client';
import { Observable } from 'rxjs';
import { environment } from '@env/environment';
import { Program } from '../../models/program';

@Injectable({
  providedIn: 'root',
})
export class ClientsService {
  private http = inject(HttpClient);

  apiURLClients = environment.apiURL + 'clients';

  getClients(): Observable<Client[]> {
    return this.http.get<Client[]>(this.apiURLClients);
  }

  getClientsBasic(): Observable<Client[]> {
    return this.http.get<Client[]>(`${this.apiURLClients}/basic`);
  }

  getClient(clientId: string): Observable<Client> {
    return this.http.get<Client>(`${this.apiURLClients}/${clientId}`);
  }

  createClient(clientData: Client): Observable<Client> {
    return this.http.post<Client>(this.apiURLClients, clientData);
  }

  updateClient(clientData: Client, clientId: string): Observable<Client> {
    return this.http.put<Client>(
      `${this.apiURLClients}/${clientId}`,
      clientData,
    );
  }

  deleteClient(clientId: string): Observable<Client> {
    return this.http.delete<Client>(`${this.apiURLClients}/${clientId}`);
  }

  getClientProgram(programId: string): Observable<Program> {
    return this.http.get<Program>(`${this.apiURLClients}/program/${programId}`);
  }

  getAllClientsPrograms(): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURLClients}/programs/all`);
  }

  getAllClientsProgramsBasic(): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURLClients}/programs/all/basic`);
  }

  updateClientProgram(
    clientId: string,
    updatedProgram: any,
    programPosition: number,
  ): Observable<any> {
    // Pass program position using URL params
    const positionParam = programPosition;
    return this.http.put<any>(
      `${this.apiURLClients}/program/${clientId}?pos=${positionParam}`,
      updatedProgram,
    );
  }

  deleteClientProgram(
    clientId: string,
    programPosition: number,
  ): Observable<any> {
    // Pass program position using URL params
    const positionParam = programPosition;
    return this.http.delete<any>(
      `${this.apiURLClients}/program/${clientId}?pos=${positionParam}`,
    );
  }
}
