import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Exercise } from '../models/exercise';

@Injectable({
  providedIn: 'root',
})
export class ShareDataService {
  private data$ = new BehaviorSubject<Exercise[]>([]);
  searchedData$ = this.data$.asObservable();

  private data2$ = new BehaviorSubject<Exercise[]>([]);
  filterData$: Observable<Exercise[]> | [] = this.data2$.asObservable();

  // private data3$ = new BehaviorSubject<boolean>(false);
  // toggleDrawerSignal$: Observable<boolean> = this.data3$.asObservable();

  constructor() {}

  updateSearchResults(data: Exercise[]) {
    this.data$.next(data);
  }

  updateFilterResults(data2: any) {
    this.data2$.next(data2);
  }

  // updateToggleDrawerResults(data3: any) {
  //   this.data3$.next(data3);
  // }
}
