import { Component, inject } from '@angular/core';
import {
  MAT_DIALOG_DATA,
  MatDialogRef,
  MatDialogTitle,
  MatDialogContent,
  MatDialogActions,
} from '@angular/material/dialog';
import { MatButton } from '@angular/material/button';
import { CdkScrollable } from '@angular/cdk/scrolling';
import { MatIcon } from '@angular/material/icon';

interface DialogData {
  title: string;
  message: string;
}

@Component({
    selector: 'ui-delete-confirmation-dialog',
    templateUrl: './deleteConfirmationDialog.component.html',
    styleUrl: './deleteConfirmationDialog.component.scss',
    imports: [
        MatIcon,
        MatDialogTitle,
        CdkScrollable,
        MatDialogContent,
        MatDialogActions,
        MatButton,
    ]
})
export class DeleteConfirmationDialogComponent {
  dialogRef =
    inject<MatDialogRef<DeleteConfirmationDialogComponent>>(MatDialogRef);
  data = inject<DialogData>(MAT_DIALOG_DATA);

  onConfirm(): void {
    this.dialogRef.close(true);
  }

  onClose(): void {
    this.dialogRef.close();
  }
}
