import { Injectable, signal } from '@angular/core';

type ListOption = 'all' | 'selected' | 'favorite';

@Injectable({
  providedIn: 'root',
})
export class ShareStateService {
  // Manage drawer state with signal
  drawerSignal = signal<boolean>(false);

  listOption = signal<ListOption>('all');

  isFetchingData = signal<boolean>(false);

  isLoading = signal<boolean>(false);

  constructor() {}
}
