import { Injectable, inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';
import { StartingPosition } from '../../models/startingPosition';

@Injectable({
  providedIn: 'root',
})
export class StartingPositionsService {
  private http = inject(HttpClient);

  apiURLStartingPositions = environment.apiURL + 'startingpositions';

  getStartingPositions(): Observable<StartingPosition[]> {
    return this.http.get<StartingPosition[]>(this.apiURLStartingPositions);
  }

  getStartingPosition(startingPositionId: string): Observable<StartingPosition> {
    return this.http.get<StartingPosition>(`${this.apiURLStartingPositions}/${startingPositionId}`);
  }

  createStartingPosition(startingPosition: StartingPosition) {
    return this.http.post(this.apiURLStartingPositions, startingPosition);
  }

  updateStartingPosition(startingPosition: StartingPosition): Observable<StartingPosition> {
    return this.http.put<StartingPosition>(
      `${this.apiURLStartingPositions}/${startingPosition.id}`,
      startingPosition
    );
  }

  deleteStartingPosition(startingPositionId: string): Observable<StartingPosition> {
    return this.http.delete<StartingPosition>(`${this.apiURLStartingPositions}/${startingPositionId}`);
  }
}
