import { Injectable, inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';
import { BodyArea } from '../../models/bodyArea';

@Injectable({
  providedIn: 'root'
})
export class BodyAreasService {
  private http = inject(HttpClient);

  apiURLBodyAreas = environment.apiURL + 'bodyareas';

  getBodyAreas(): Observable<BodyArea[]> {
    return this.http.get<BodyArea[]>(this.apiURLBodyAreas);
  }

  getBodyArea(bodyAreaId: string): Observable<BodyArea> {
    return this.http.get<BodyArea>(`${this.apiURLBodyAreas}/${bodyAreaId}`);
  }

  createBodyArea(bodyArea: BodyArea) {
    return this.http.post(this.apiURLBodyAreas, bodyArea);
  }

  updateBodyArea(bodyArea: BodyArea): Observable<BodyArea> {
    return this.http.put<BodyArea>(`${this.apiURLBodyAreas}/${bodyArea.id}`, bodyArea);
  }

  deleteBodyArea(bodyAreaId: string): Observable<BodyArea> {
    return this.http.delete<BodyArea>(`${this.apiURLBodyAreas}/${bodyAreaId}`);
  }
}
