import { Injectable, inject, signal } from '@angular/core';
import { ShareDataService } from './share-data.service';

interface DataItem {
  name: string | undefined;
  localeNames: string[];
  values: string[];
}

@Injectable({
  providedIn: 'root',
})
export class AddRemoveDataService {
  private sharedDataService = inject(ShareDataService);

  // private data: DataItem[] = [];
  public dataSignal = signal<DataItem[]>([]);

  addDataItem(
    name: string | undefined,
    value: string,
    localeName: string,
  ): void {
    const itemIndex = this.dataSignal().findIndex(item => item.name === name);
    console.log(name, value);
    if (itemIndex !== -1) {
      // If the item already exists, add the value to its array
      this.dataSignal.update(data => {
        const updatedData = [...data];
        updatedData[itemIndex].values.push(value);
        updatedData[itemIndex].localeNames.push(localeName);

        console.log('when updateng: ', data === updatedData);
        return updatedData;
      });
      console.log(this.dataSignal());
      // this.data[itemIndex].values.push(value);
      // this.data[itemIndex].localeNames.push(localeName);
    } else {
      // If the item does not exist, create a new one
      this.dataSignal.update(data => {
        const updatedData = [...data];
        console.log('when adding new: ', data === updatedData);

        updatedData.push({ name, values: [value], localeNames: [localeName] });
        return updatedData;
      });
      console.log(this.dataSignal());
    }
    this.sharedDataService.updateFilterResults(this.dataSignal());
  }

  removeDataItem(
    name: string | undefined,
    value: string,
    localeName: string,
  ): void {
    const itemIndex = this.dataSignal().findIndex(item => item.name === name);

    if (itemIndex !== -1) {
      // If the item exists, remove the value from its array
      this.dataSignal.update(data => {
        const updatedData = [...data];
        console.log('this is updated before removal: ', updatedData);

        // Update latin name(value)
        updatedData[itemIndex].values = updatedData[itemIndex].values.filter(
          (v: any) => v !== value,
        );

        console.log('this is updated after removal: ', updatedData);

        // Update locale name(localeName)
        updatedData[itemIndex].localeNames = updatedData[
          itemIndex
        ].localeNames.filter((v: any) => v !== localeName);

        return updatedData;
      });

      // Remove the entire item if the values array is empty
      if (this.dataSignal()[itemIndex].values.length === 0) {
        this.dataSignal.update(data => {
          const updatedData = [...data];

          updatedData.splice(itemIndex, 1);

          return updatedData;
        });
      }
    }
    this.sharedDataService.updateFilterResults(this.dataSignal());
  }

  // getData(): DataItem[] {
  //   return this.data;
  // }

  resetAll(): void {
    this.dataSignal.set([]);
    this.sharedDataService.updateFilterResults(this.dataSignal());
  }
}
