import { Component, inject, OnDestroy, OnInit } from '@angular/core';
import {
  MatDialog,
  MAT_DIALOG_DATA,
  MatDialogRef,
  MatDialogTitle,
  MatDialogActions,
  MatDialogClose,
} from '@angular/material/dialog';
// eslint-disable-next-line @nx/enforce-module-boundaries
import {
  Exercise,
  ExerciseModalComponent,
  ExercisesService,
  Program,
} from '@slowautomobile/exercises';
import { ThemeManagerService } from '../../services/theme-manager.service';
import { MatIcon } from '@angular/material/icon';
import { MatTooltip } from '@angular/material/tooltip';
import { MatIconButton, MatFabButton } from '@angular/material/button';
import {
  MatAccordion,
  MatExpansionPanel,
  MatExpansionPanelHeader,
  MatExpansionPanelTitle,
  MatExpansionPanelDescription,
} from '@angular/material/expansion';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { Observable, Subscription } from 'rxjs';

interface DialogData {
  title: string;
  program: Program;
  programId: string;
  selectedLang: 'en' | 'de' | 'sr';
}

@Component({
    selector: 'ui-program-modal',
    templateUrl: './program-modal.component.html',
    styleUrl: './program-modal.component.scss',
    imports: [
        MatDialogTitle,
        OverlayscrollbarsModule,
        MatAccordion,
        MatExpansionPanel,
        MatExpansionPanelHeader,
        MatExpansionPanelTitle,
        MatExpansionPanelDescription,
        MatIconButton,
        MatTooltip,
        MatIcon,
        MatDialogActions,
        MatFabButton,
        MatDialogClose,
    ]
})
export class ProgramModalComponent implements OnInit, OnDestroy {
  dialogRef = inject<MatDialogRef<ProgramModalComponent>>(MatDialogRef);
  data = inject<DialogData>(MAT_DIALOG_DATA);
  dialog = inject(MatDialog);
  private exercisesService = inject(ExercisesService);

  subscriptions = new Subscription();

  // program!: Program;
  isExpanded = false;
  exerciseId!: string;

  themeManager = inject(ThemeManagerService);
  themeSignal = this.themeManager.themeSignal;

  ngOnInit(): void {
    //   this.program = this.data.program;
    console.log(this.data);
  }

  addProgramToClient(programId: string | undefined) {
    console.log(programId);
  }

  private _getExercise(exerciseId: string): Observable<Exercise> {
    return this.exercisesService.getExercise(exerciseId);
  }

  showExerciseModal(
    exerciseId: string,
    exerciseCard: Exercise,
    isProgramExercise: boolean,
  ): void {
    const subscription = this._getExercise(exerciseId).subscribe(exercise => {
      this.dialog.open(ExerciseModalComponent, {
        data: {
          fullExercise: exercise,
          exerciseCard: exerciseCard,
          isProgramExercise: isProgramExercise,
          selectedLang: this.data.selectedLang,
        },
        width: '500px',
        // height: '736px',
        panelClass: [
          'custom-dialog',
          ...(isProgramExercise
            ? ['custom-shadow', 'custom-exercise-dialog-content-wrapper']
            : []),
        ],
        // disableClose: isProgramExercise,
        // hasBackdrop: isProgramExercise ? false : true,
        backdropClass: isProgramExercise ? 'custom-backdrop' : '',
        // enterAnimationDuration: 0,
      });
    });
    this.subscriptions.add(subscription);
  }

  onClose(): void {
    this.dialogRef.close();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
