import { BreakpointObserver } from '@angular/cdk/layout';
import { inject, Injectable, signal } from '@angular/core';
import { map, Observable, shareReplay, tap } from 'rxjs';
import { toSignal } from '@angular/core/rxjs-interop';

type ScreenSize = {
  isCompact: boolean;
  isMedium: boolean;
  isExpanded: boolean;
  //  islargeBP: boolean;
  //  isextraLargeBP: boolean;
};

@Injectable({
  providedIn: 'root',
})
export class ScreenSizeService {
  private breakpointObserver = inject(BreakpointObserver);

  // Breakpoints
  private compactBP = '(max-width: 599px)';
  private mediumBP = '(min-width: 600px) and (max-width: 839px)';
  private expandedBP = '(min-width: 840px) and (max-width: 1199px)';
  // private largeBP = '(min-width: 1200px) and (max-width: 1599px)';
  // private extraLargeBP = '(min-width: 1600px)';

  screenSize = toSignal(
    this.breakpointObserver
      .observe([this.compactBP, this.mediumBP, this.expandedBP])
      .pipe(
        map(result => ({
          isCompact: result.breakpoints[this.compactBP],
          isMedium: result.breakpoints[this.mediumBP],
          isExpanded: result.breakpoints[this.expandedBP],
        })),
        shareReplay({ bufferSize: 1, refCount: true }),
        tap(result => console.log(result)),
      ),
  );
}
