import {
  Component,
  effect,
  EventEmitter,
  inject,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import {
  MatButtonToggle,
  MatButtonToggleGroup,
} from '@angular/material/button-toggle';
import { SearchComponent } from '../../pages/search/search.component';
import { FormsModule } from '@angular/forms';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { ShareStateService } from '@slowautomobile/exercises';

type ListOption = 'all' | 'selected' | 'favorite';

@Component({
    selector: 'ui-exercise-search-btns-wrapper',
    imports: [
        CommonModule,
        FormsModule,
        MatButtonToggleGroup,
        MatButtonToggle,
        SearchComponent,
    ],
    templateUrl: './exercise-search-btns-wrapper.component.html',
    styleUrl: './exercise-search-btns-wrapper.component.scss'
})
export class ExerciseSearchBtnsWrapperComponent {
  private shareStateService = inject(ShareStateService);

  listOptionSignal = this.shareStateService.listOption;
  drawerOpen!: boolean;
  listOption!: ListOption;

  @Output() listOptionValue = new EventEmitter<ListOption>();

  constructor() {
    effect(() => {
      this.listOption = this.listOptionSignal();
    });
  }

  changeListOption(listOptionValue: ListOption) {
    this.listOptionSignal.set(listOptionValue);
    this.listOptionValue.emit(listOptionValue);
  }

  toggleDrawer() {
    this.drawerOpen = !this.drawerOpen;
  }
}
