  getPrograms(listOption: string) {
    this.isFetchingData.set(true);

    if (listOption === 'template') {
      const subscription = this.programsService
        .getPrograms()
        .subscribe(programsList => {
          this.allPrograms = [];
          this.allPrograms = programsList;
          this.allPrograms.sort((a, b) => {
            return a.name.localeCompare(b.name);
          });
          console.log(this.allPrograms);

          this.dataSourceAllPrograms.data = this.allPrograms;
          this.isFetchingData.set(false);
        });
      this.subscriptions.add(subscription);
    }
  }

  viewProgramModal(programId: string) {
    const subscription = this.programsService
      .getProgram(programId)
      .subscribe(program => {
        const dialogRef = this.dialog.open(ProgramModalComponent, {
          width: '500px',
          // height: '736px',
          data: {
            title: $localize`:@@ProgramModal.modalTitle:Program info`,
            program: program,
            selectedLang: this.selectedLanguage,
          },
          // disableClose: true, // Disable closing when clicked outside
          panelClass: 'custom-dialog',
          // enterAnimationDuration: 0,
        });

        const subscription = dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.addProgramToClient(result);
          } else {
            console.log('Program modal exited');
          }
        });
        this.subscriptions.add(subscription);
      });
    this.subscriptions.add(subscription);
  }

  addProgramToClient(programId: string) {
    console.log(programId);
    const subscription = this.programsService
      .getProgram(programId)
      .subscribe(program => {
        program.isTemplate = false;
        console.log(program);
        // Create independent program that belongs to client with its unique id and configuration
        let startDate = this.form.get('clientInfo.dateRange.start')?.value;
        console.log(startDate === null);
        let endDate = this.form.get('clientInfo.dateRange.end')?.value;
        startDate === undefined ? (startDate = new Date()) : startDate;
        endDate === undefined
          ? (endDate = startDate.getTime() + 7 * 24 * 60 * 60 * 1000)
          : endDate;
        const newProgram = {
          // programId: createdProgram.id,
          // programName: createdProgram.name,
          program: {
            name: program.name,
            diagnosis: program.diagnosis,
            notes: program.notes,
            selectedExercises: program.selectedExercises,
            templateProgramId: program.id,
            isTemplate: false,
          },
          dateRange: {
            // start: dateRange.start,
            // end: dateRange.end,
            start: startDate,
            end: endDate,
          },
        };
        console.log(newProgram);
        const programs = this.form.value;
        const updatedPrograms = [...programs, newProgram];
        console.log(updatedPrograms);
        const updatedClient = {
          firstName: this.client.firstName,
          lastName: this.client.lastName,
          name: this.client.name,
          email: this.client.email,
          phone: this.client.phone,
          address: {
            street: this.client.address?.street,
            streetNumber: this.client.address?.streetNumber,
            city: this.client.address?.city,
            zipCode: this.client.address?.zipCode,
            country: this.client.address?.country,
          },
          programs: updatedPrograms,
        };

        const subscription = this.clientsService
          .updateClient(updatedClient, this.clientId)
          .subscribe(updatedClient => {
            // Add full newly added client's program to form
            const newAddedProgram = updatedClient.programs?.pop();
            const programsArray = this.form as FormArray;
            programsArray.push(new FormControl(newAddedProgram));

            // this.parentForm.updateValueAndValidity();
            this.getClientsPrograms();

            const message = $localize`:@@Snackbar.programAddedToClient:Program added to client`;
            this._snackBar.open(message, '', {
              duration: 2000,
              panelClass: ['custom-snackbar-style'],
            });
          });
        this.subscriptions.add(subscription);

        // lastValueFrom(timer(2100)).then(() => {
        //   this.router.navigateByUrl(`clients/form/${this.clientId}`);
        // });
      });
    this.subscriptions.add(subscription);
  }

  private _getUserLanguageToken(): 'en' | 'de' | 'sr' {
    return <'en' | 'de' | 'sr'>(
      this.localstorageService.getToken('userLanguage')?.split('-')[0]
    );
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
