import { Injectable, signal } from '@angular/core';
import { Exercise } from '../models/exercise';

@Injectable({
  providedIn: 'root',
})
export class SelectedExercisesService {
  selectedExercises = signal<Exercise[]>([]);

  // Add exercise to selected list
  addToSelected(exercise: Exercise): void {
    console.log(exercise);
    // Check if exercise is already in the list
    const index = this.selectedExercises().findIndex(
      selectedExercise => selectedExercise.id === exercise.id,
    );

    // Not already in the list, so add it
    if (index === -1) {
      exercise.isSelected = true;
      exercise.reps = 10;
      exercise.sets = 3;
      exercise.pause = '1min';
      exercise.weight = '';
      this.selectedExercises.update(selectedExercises => [
        ...selectedExercises,
        exercise,
      ]);
    }
  }

  removeFromSelected(exercise: Exercise): void {
    exercise.isSelected = false;
    this.selectedExercises.update(selectedExercises =>
      selectedExercises.filter(
        selectedExercise => selectedExercise.id !== exercise.id,
      ),
    );
  }

  // updateInSelected(exercise: Exercise): void {
  //   exercise.isSelected = true;
  //   this.selectedExercises.update(selectedExercises =>
  //     selectedExercises.map(selectedExercise =>
  //       selectedExercise.id === exercise.id
  //         ? (selectedExercise = exercise)
  //         : selectedExercise,
  //     ),
  //   );
  // }
}
