import { Injectable, inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';
import { Equipment } from '../../models/equipment';

@Injectable({
  providedIn: 'root',
})
export class EquipmentService {
  private http = inject(HttpClient);

  apiURLEquipments = environment.apiURL + 'equipments';

  getEquipments(): Observable<Equipment[]> {
    return this.http.get<Equipment[]>(this.apiURLEquipments);
  }

  getEquipment(equipmentId: string): Observable<Equipment> {
    return this.http.get<Equipment>(`${this.apiURLEquipments}/${equipmentId}`);
  }

  createEquipment(equipment: Equipment) {
    return this.http.post(this.apiURLEquipments, equipment);
  }

  updateEquipment(equipment: Equipment): Observable<Equipment> {
    return this.http.put<Equipment>(
      `${this.apiURLEquipments}/${equipment.id}`,
      equipment
    );
  }

  deleteEquipment(equipmentId: string): Observable<Equipment> {
    return this.http.delete<Equipment>(`${this.apiURLEquipments}/${equipmentId}`);
  }
}
