import { Component, EventEmitter, input, Output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatTooltipModule } from '@angular/material/tooltip';

@Component({
    selector: 'ui-dynamic-fab-button',
    imports: [CommonModule, MatButtonModule, MatIconModule, MatTooltipModule],
    templateUrl: './dynamic-fab-button.component.html',
    styleUrl: './dynamic-fab-button.component.scss'
})
export class DynamicFabButtonComponent {
  isScrolling = input.required<boolean>();
  matTooltip = input<string>('');
  newButtonString = input<string>('');

  @Output() newFabEvent = new EventEmitter<void>();
}
