import { Injectable, signal } from '@angular/core';

export interface ClientInfo {
  clientId: string;
  clientName: string;
  programPosition: number;
  programId: string;
  programName: string;
  dateRange: {
    start: string;
    end: string;
  };
}

@Injectable({
  providedIn: 'root',
})
export class ShareClientsDataService {
  clients = signal<any[]>([]);

  isClientProgram = signal<boolean>(false);

  clientInfo = signal<ClientInfo>({
    clientId: '',
    clientName: '',
    programPosition: 0,
    programId: '',
    programName: '',
    dateRange: { start: '', end: '' },
  });

  reuseProgram = signal<boolean>(false);

  // Use this signal to control new program component, so that it automatically set client, but leave all other fields empty
  // IMPLEMENT THIS !!!!!
  isBrandNewClientsProgram = signal<boolean>(false);
}
