import { Injectable, inject, signal } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '@env/environment';
import { Program } from '../../models/program';

@Injectable({
  providedIn: 'root',
})
export class ProgramsService {
  private http = inject(HttpClient);

  apiURLPrograms = environment.apiURL + 'programs';

  getPrograms(): Observable<Program[]> {
    return this.http.get<Program[]>(this.apiURLPrograms);
  }

  getProgram(programId: string): Observable<Program> {
    return this.http.get<Program>(`${this.apiURLPrograms}/${programId}`);
  }

  createProgram(programData: Program): Observable<Program> {
    return this.http.post<Program>(this.apiURLPrograms, programData);
  }

  updateProgram(programData: Program, programId: string): Observable<Program> {
    return this.http.put<Program>(
      `${this.apiURLPrograms}/${programId}`,
      programData,
    );
  }

  deleteProgram(programId: string): Observable<Program> {
    return this.http.delete<Program>(`${this.apiURLPrograms}/${programId}`);
  }

  isAdminProgram = signal<boolean>(false);
}
