import { Component, inject } from '@angular/core';
import {
  MAT_DIALOG_DATA,
  MatDialogClose,
  MatDialogTitle,
  MatDialogActions,
} from '@angular/material/dialog';
import { Exercise } from '../../models/exercise';
import { SelectedExercisesService } from '../../services/selected-exercises.service';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { ThemeManagerService, VideoPlayerComponent } from '@slowautomobile/ui';
import { MatChipSet, MatChip } from '@angular/material/chips';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { MatIcon } from '@angular/material/icon';
import { MatTooltip } from '@angular/material/tooltip';
import { MatIconButton, MatFabButton } from '@angular/material/button';
import { NgClass } from '@angular/common';

export interface DialogData {
  fullExercise: Exercise;
  exerciseCard: Exercise;
  isProgramExercise: boolean;
  selectedLang: 'en' | 'de' | 'sr';
}

@Component({
    selector: 'exercises-single-modal',
    templateUrl: './exercise-modal.component.html',
    imports: [
        NgClass,
        MatIconButton,
        MatTooltip,
        MatDialogClose,
        MatIcon,
        MatDialogTitle,
        OverlayscrollbarsModule,
        MatChipSet,
        MatChip,
        MatDialogActions,
        MatFabButton,
        VideoPlayerComponent
    ]
})
export class ExerciseModalComponent {
  data = inject<DialogData>(MAT_DIALOG_DATA);
  private selectedExercisesService = inject(SelectedExercisesService);
  themeManager = inject(ThemeManagerService);

  selectedLang: 'en' | 'de' | 'sr' = this.data.selectedLang;
  themeSignal = this.themeManager.themeSignal;
  isHovered = false;

  addToSelected(exercise: Exercise) {
    this.selectedExercisesService.addToSelected(exercise);
    console.log(this.selectedExercisesService.selectedExercises());
  }

  removeFromSelected(exercise: Exercise) {
    this.selectedExercisesService.removeFromSelected(exercise);
    console.log(this.selectedExercisesService.selectedExercises());
  }

  showHoverIcon(isHovered: boolean): void {
    this.isHovered = isHovered;
    // console.log(isHovered)
  }
}
