import { Injectable, inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';
import { Movement } from '../../models/movement';

@Injectable({
  providedIn: 'root',
})
export class MovementsService {
  private http = inject(HttpClient);

  apiURLMovements = environment.apiURL + 'movements';

  getMovements(): Observable<Movement[]> {
    return this.http.get<Movement[]>(this.apiURLMovements);
  }

  getMovement(movementId: string): Observable<Movement> {
    return this.http.get<Movement>(`${this.apiURLMovements}/${movementId}`);
  }

  createMovement(movement: Movement) {
    return this.http.post(this.apiURLMovements, movement);
  }

  updateMovement(movement: Movement): Observable<Movement> {
    return this.http.put<Movement>(
      `${this.apiURLMovements}/${movement.id}`,
      movement
    );
  }

  deleteMovement(movementId: string): Observable<Movement> {
    return this.http.delete<Movement>(`${this.apiURLMovements}/${movementId}`);
  }
}
