// ];

@Component({
    selector: 'ui-filter-select-clients',
    templateUrl: './filter-select-clients.component.html',
    styleUrl: './filter-select-clients.component.scss',
    imports: [
        MatFormField,
        MatLabel,
        MatIcon,
        MatPrefix,
        MatSelect,
        FormsModule,
        ReactiveFormsModule,
        MatOption,
        NgxMatSelectSearchModule,
        AsyncPipe,
    ]
})
export class FilterSelectClientsComponent
  implements OnInit, OnDestroy, AfterViewInit
{
  private shareClientsDataService = inject(ShareClientsDataService);
  private clientsService = inject(ClientsService);
  private controlContainer = inject(ControlContainer);

  /** list of clients */
  clients: Client[] = [];
  // clients = this.shareClientsDataService.clients;
  /** control for the selected client */
  public clientCtrl!: FormControl;
  subscriptions = new Subscription();

  /** control for the MatSelect filter keyword */
  public clientFilterCtrl: FormControl = new FormControl();

  /** list of clients filtered by search keyword */
  public filteredClients: ReplaySubject<Client[]> = new ReplaySubject<Client[]>(
    1,
  );

  reuseProgram = this.shareClientsDataService.reuseProgram;

  // Emit new value to parent on change
  // @Output() valueChange = new EventEmitter<string>();

  @ViewChild('singleSelect', { static: true })
  singleSelect!: MatSelect;

  /** Subject that emits when the component has been destroyed. */
  protected _onDestroy = new Subject<void>();

  /** flags to set the toggle all checkbox state */
  isIndeterminate = false;
  isChecked = false;

  isClientProgramSignal = this.shareClientsDataService.isClientProgram;
  clientInfo = this.shareClientsDataService.clientInfo;

  ngOnInit() {
    this.clientCtrl = this.controlContainer.control?.get(
      'selectedClients',
    ) as FormControl;
    console.log(this.clientCtrl);
    console.log(this.clientInfo());

    // this.clientCtrl.setValidators([Validators.required]);
    // this.clientCtrl.updateValueAndValidity();
    // set initial selection
    // this.clientCtrl.setValue([this.clients[1], this.clients[2]]);
    if (this.isClientProgramSignal() && !this.reuseProgram()) {
      const subscription = this.clientsService
        .getClient(this.clientInfo().clientId)
        .subscribe(client => {
          // Return a whole client document, so that createProgram/updateProgram can have programs property
          console.log('this is client', client);

          this.clientCtrl.setValue([
            client,
            // { name: this.clientInfo().clientName, id: this.clientInfo().clientId },
          ]);
        });
      this.subscriptions.add(subscription);
    }

    const subscription = this.clientsService
      .getClients()
      .subscribe(clientsList => {
        // clientsList.forEach(client => {
        //   this.clients.push({
        //     id: client.id,
        //     name: `${client.lastName} ${client.firstName}`,
        //   });
        // });
        this.clients = clientsList;
        console.log(this.clients);
        // load the initial client list
        this.filteredClients.next(this.clients.slice());
      });
    this.subscriptions.add(subscription);

    // this.clientCtrl.valueChanges.subscribe(clients => {
    //   this.valueChange.emit(clients)
    // });

    // listen for search field value changes
    this.clientFilterCtrl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterClients();
      });
  }

  ngAfterViewInit() {
    this.setInitialValue();
    // this.filteredClients.next(this.clients().slice());
    // console.log(this.clients());
    /// TRY TO LAZY LOAD COMPONENT, MODULE OR TO MAKE STEPS IN STEPPER DIFFERENT COMPONENTS??? IT WORKS, REFACTOR MORE ELEGANTLY!
  }

  /**
   * Sets the initial value after the filteredClients are loaded initially
   */
  protected setInitialValue() {
    this.filteredClients
      .pipe(take(1), takeUntil(this._onDestroy))
      .subscribe(() => {
        this.singleSelect.compareWith = (a: Client, b: Client) =>
          a && b && a.id === b.id;
      });
  }

  protected filterClients() {
    if (!this.clients) {
      return;
    }
    // get the search keyword
    let search = this.clientFilterCtrl.value;
    if (!search) {
      this.filteredClients.next(this.clients.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the clients
    this.filteredClients.next(
      this.clients.filter(
        client => client.name.toLowerCase().indexOf(search) > -1,
      ),
    );
  }

  toggleSelectAll(selectAllValue: boolean) {
    this.filteredClients
      .pipe(take(1), takeUntil(this._onDestroy))
      .subscribe(val => {
        if (selectAllValue) {
          this.clientCtrl.patchValue(val);
        } else {
          this.clientCtrl.patchValue([]);
        }
      });
  }

  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();
    this.subscriptions.unsubscribe();
  }
}
