import {
  AfterViewInit,
  Component,
  ElementRef,
  Input,
  ViewChild,
} from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
    selector: 'ui-video-player',
    imports: [CommonModule],
    templateUrl: './video-player.component.html',
    styleUrl: './video-player.component.scss'
})
export class VideoPlayerComponent implements AfterViewInit {
  @ViewChild('videoPlayer', { static: false })
  videoPlayer!: ElementRef<HTMLVideoElement>;
  @Input() videoSrc!: string | undefined;
  @Input() thumbnail?: string | undefined;
  @Input() isModal? = true;
  private video!: HTMLVideoElement;
  isPlaying = false;
  private hoverTimeout: any;

  ngAfterViewInit() {
    this.video = this.videoPlayer.nativeElement;
  }

  onMouseEnter() {
    if (!this.isModal) {
      this.hoverTimeout = setTimeout(() => {
        this.playVideo();
      }, 500);
    }
  }

  onMouseLeave() {
    if (!this.isModal) {
      clearTimeout(this.hoverTimeout);
      this.pauseVideo();
    }
  }

  // Play video on mouse enter
  async playVideo() {
    // if (this.isPlaying) return; // Prevents unnecessary restarts
    if (!this.isModal && !this.isPlaying) {
      try {
        // Load the video explicitly
        this.video.load();

        // Wait for the video to be ready
        await new Promise<void>(resolve => {
          this.video.addEventListener('canplay', () => resolve(), {
            once: true,
          });
        });

        // Attempt to play the video
        this.isPlaying = true;
        await this.video.play();
        this.video.loop = true;
        // console.log('Video is playing');
      } catch (error) {
        console.error('Video playback failed:', error);
      }
    }
  }

  // Pause video on mouse leave
  pauseVideo() {
    this.isPlaying = false;
    this.video.pause();
    this.video.loop = false;
    // console.log('Video is paused');
  }
}
