interface CategoryNode {
  name: string;
  localeName?: string;
  children?: CategoryNode[];
  isChecked?: boolean;
  categoryName?: string;
  categoryNameLocale?: string;
  values?: string[];
  localeNames?: string[];
}

interface CategoryFlatNode {
  expandable: boolean;
  name: string;
  // localeName: string;
  level: number;
  isChecked: boolean | undefined;
  categoryName: CategoryNode['categoryName'];
  // categoryNameLocale: CategoryNode['categoryNameLocale'];
}

interface CategoryGroup {
  name: string;
  value: string[];
}

