import { Component, inject } from '@angular/core';
import { CommonModule, Location } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatMenuModule } from '@angular/material/menu';
import { MatToolbarModule } from '@angular/material/toolbar';
import { AuthService, UsersService } from '@slowautomobile/users';
import { Router, RouterLink } from '@angular/router';
import { ThemeManagerService } from '../../services/theme-manager.service';
import { ActivePageService } from '../../services/active-page.service';
import { ScreenSizeService } from '../../services/screen-size.service';

@Component({
    selector: 'ui-toolbar-buttons',
    imports: [
        CommonModule,
        MatIconModule,
        MatButtonModule,
        MatToolbarModule,
        MatMenuModule,
        RouterLink,
    ],
    templateUrl: './toolbar-buttons.component.html',
    styleUrl: './toolbar-buttons.component.scss'
})
export class ToolbarButtonsComponent {
  private usersService = inject(UsersService);
  private authService = inject(AuthService);
  themeManager = inject(ThemeManagerService);
  private activePageTitle = inject(ActivePageService);
  private location = inject(Location);
  private router = inject(Router);
  private screenSizeService = inject(ScreenSizeService);

  // @Input() pageTitle?: string;
  screenSize = this.screenSizeService.screenSize;
  pageTitle = this.activePageTitle.pageTitle;
  backNavigation = this.activePageTitle.backNavigation;

  isDark = this.themeManager.isDark;
  userName = this.usersService.userName;

  getInitial(name: string): string {
    return name.trim().charAt(0).toUpperCase();
  }

  onBack() {
    // this.location.back();
    this.router.navigateByUrl('/');
  }

  logoutUser() {
    this.authService.logout();
  }
}
