import { Injectable, inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Exercise } from '../models/exercise';
import { Muscle } from '../models/muscle';
import { BodyArea } from '../models/bodyArea';
import { Movement } from '../models/movement';
import { StartingPosition } from '../models/startingPosition';
import { Equipment } from '../models/equipment';
import { environment } from '@env/environment';

@Injectable({
  providedIn: 'root',
})
export class ExercisesService {
  private http = inject(HttpClient);

  apiURLExercises = environment.apiURL + 'exercises';
  apiURLMuscles = environment.apiURL + 'muscles';
  apiURLBodyAreas = environment.apiURL + 'bodyareas';
  apiURLMovements = environment.apiURL + 'movements';
  apiURLStartingPositions = environment.apiURL + 'startingpositions';
  apiURLEquipments = environment.apiURL + 'equipments';

  getExercises(): Observable<Exercise[]> {
    return this.http.get<Exercise[]>(this.apiURLExercises);
  }

  getExercise(exerciseId: string): Observable<Exercise> {
    // const options = { headers: headers };

    return this.http.get<Exercise>(
      `${this.apiURLExercises}/${exerciseId}`
    );
  }

  createExercise(exerciseData: FormData): Observable<Exercise> {
    return this.http.post<Exercise>(this.apiURLExercises, exerciseData);
  }

  updateExercise(
    exerciseData: FormData,
    exerciseId: string
  ): Observable<Exercise> {
    return this.http.put<Exercise>(
      `${this.apiURLExercises}/${exerciseId}`,
      exerciseData
    );
  }

  deleteExercise(exerciseId: string): Observable<Exercise> {
    return this.http.delete<Exercise>(`${this.apiURLExercises}/${exerciseId}`);
  }

  getMuscles(): Observable<Muscle[]> {
    return this.http.get<Muscle[]>(this.apiURLMuscles);
  }

  createMuscle(muscle: Muscle) {
    return this.http.post(this.apiURLMuscles, muscle);
  }

  getBodyAreas(): Observable<BodyArea[]> {
    // const options = { headers: headers };

    return this.http.get<BodyArea[]>(this.apiURLBodyAreas);
  }

  createBodyArea(bodyArea: BodyArea) {
    return this.http.post(this.apiURLBodyAreas, bodyArea);
  }

  getMovements(): Observable<Movement[]> {
    // const options = { headers: headers };

    return this.http.get<Movement[]>(this.apiURLMovements);
  }

  createMovement(movement: Movement) {
    return this.http.post(this.apiURLMovements, movement);
  }

  getStartingPositions(): Observable<StartingPosition[]> {
    // const options = { headers: headers };

    return this.http.get<StartingPosition[]>(
      this.apiURLStartingPositions
    );
  }

  createStartingPosition(startingPosition: StartingPosition) {
    return this.http.post(this.apiURLStartingPositions, startingPosition);
  }

  getEquipments(): Observable<Equipment[]> {
    // const options = { headers: headers };

    return this.http.get<Equipment[]>(this.apiURLEquipments);
  }

  createEquipment(equipment: Equipment) {
    return this.http.post(this.apiURLEquipments, equipment);
  }
}
