import {
  AfterViewInit,
  Component,
  inject,
  Input,
  OnDestroy,
  OnInit,
  ViewChild,
} from '@angular/core';
import {
  ControlContainer,
  FormControl,
  FormGroup,
  FormsModule,
  ReactiveFormsModule,
} from '@angular/forms';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatSelect } from '@angular/material/select';
import { ReplaySubject, Subject, take, takeUntil } from 'rxjs';
import { LiveAnnouncer } from '@angular/cdk/a11y';
import {
  MatChipEditedEvent,
  MatChipInputEvent,
  MatChipGrid,
  MatChipRow,
  MatChipRemove,
  MatChipInput,
} from '@angular/material/chips';
import { ThemeManagerService } from '../../services/theme-manager.service';
import { MatIcon } from '@angular/material/icon';
import { UploadImageComponent } from '../upload-image/upload-image.component';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { AsyncPipe } from '@angular/common';
import { NgxMatSelectSearchModule } from 'ngx-mat-select-search';
import { MatOption } from '@angular/material/core';
import { MatInput } from '@angular/material/input';
import { MatFormField, MatLabel, MatError } from '@angular/material/form-field';
import { LocalstorageService } from '@slowautomobile/users';
import { CountriesLibService } from '../../services/countries-lib.service';

interface Country {
  id: string;
  name: string;
}

@Component({
  selector: 'ui-client-full-edit-form',
  templateUrl: './client-full-edit-form.component.html',
  styleUrl: './client-full-edit-form.component.scss',
  imports: [
    FormsModule,
    ReactiveFormsModule,
    MatFormField,
    MatLabel,
    MatInput,
    MatError,
    MatSelect,
    MatOption,
    NgxMatSelectSearchModule,
    OverlayscrollbarsModule,
    UploadImageComponent,
    MatChipGrid,
    MatChipRow,
    MatChipRemove,
    MatIcon,
    MatChipInput,
    AsyncPipe,
  ],
})
export class ClientFullEditFormComponent
  implements OnInit, OnDestroy, AfterViewInit
{
  private controlContainer = inject(ControlContainer);
  private announcer = inject(LiveAnnouncer);
  private localStorageService = inject(LocalstorageService);
  private countriesLibService = inject(CountriesLibService);

  @Input() isClient = false;
  form!: FormGroup;
  countries: Country[] = [];
  countryCtrl!: FormControl;
  countryFilterCtrl: FormControl = new FormControl();
  public filteredCountries: ReplaySubject<Country[]> = new ReplaySubject<
    Country[]
  >(1);
  isUserForm = false;
  qualifications: string[] = [];
  licences: string[] = [];
  qualificationsFormCtrl!: FormControl;
  licencesFormCtrl!: FormControl;
  addOnBlur = true;
  readonly separatorKeysCodes = [ENTER, COMMA] as const;

  @ViewChild('singleSelect', { static: true }) singleSelect!: MatSelect;

  /** Subject that emits when the component has been destroyed. */
  protected _onDestroy = new Subject<void>();

  themeManager = inject(ThemeManagerService);
  themeSignal = this.themeManager.themeSignal;

  ngOnInit(): void {
    this._getCountries();

    // listen for search field value changes
    this.countryFilterCtrl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterCountries();
      });

    this._initForm();

    // setTimeout(() => {
    // set initial selection
    // This is not needed because form is initialized in _initForm()
    // const currentCountry = this.form.get('address.country')?.value;
    // this.countryCtrl.setValue({ name: currentCountry });
  }

  private _getCountries() {
    // Dinamically check localeService for language in use and coresponding countriesLib.regsterLocale call
    const lang = <string>(
      this.localStorageService.getToken('userLanguage')?.split('-')[0]
    );

    this.countriesLibService
      .getCountriesLib(lang)
      .pipe(takeUntil(this._onDestroy))
      .subscribe(countries => {
        this.countries = countries;
        this.countries.sort((a, b) => a.name.localeCompare(b.name));

        // load the initial country list
        this.filteredCountries.next(this.countries.slice());
      });
  }

  _initForm() {
    const clientInfoForm = this.controlContainer.control?.get(
      'clientInfo',
    ) as FormGroup;

    // Check if form is coming from client-form or user-edit-form component
    if (clientInfoForm) {
      this.form = clientInfoForm as FormGroup;
      this.countryCtrl = this.form.get('address.country') as FormControl;
    } else {
      this.isUserForm = true;
      this.form = this.controlContainer.control as FormGroup;
      this.countryCtrl = this.form.get('address.country') as FormControl;
      this.qualificationsFormCtrl = this.form.get(
        'credentials.qualifications',
      ) as FormControl;
      this.licencesFormCtrl = this.form.get(
        'credentials.licences',
      ) as FormControl;

      setTimeout(() => {
        this.qualifications = this.qualificationsFormCtrl.value as string[];
        this.licences = this.licencesFormCtrl.value as string[];
      }, 500);
    }
  }

  ngAfterViewInit() {
    this.setInitialValue();
  }

  /**
   * Sets the initial value after the filteredBanks are loaded initially
   */
  protected setInitialValue() {
    this.filteredCountries
      .pipe(take(1), takeUntil(this._onDestroy))
      .subscribe(() => {
        // setting the compareWith property to a comparison function
        // triggers initializing the selection according to the initial value of
        // the form control (i.e. _initializeSelection())
        // this needs to be done after the filteredCountries are loaded initially
        // and after the mat-option elements are available
        this.singleSelect.compareWith = (a: Country, b: Country) =>
          a && b && a === b;
      });
  }

  protected filterCountries() {
    if (!this.countries) {
      return;
    }
    // get the search keyword
    let search = this.countryFilterCtrl.value;
    if (!search) {
      this.filteredCountries.next(this.countries.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the countries
    this.filteredCountries.next(
      this.countries.filter(
        country => country.name.toLowerCase().indexOf(search) > -1,
      ),
    );
  }

  removeQualification(qualification: string) {
    const index = this.qualifications.indexOf(qualification);
    if (index >= 0) {
      this.qualifications.splice(index, 1);

      this.announcer.announce(`removed ${qualification}`);
    }
  }

  addQualification(event: MatChipInputEvent): void {
    const value = (event.value || '').trim();

    // Add our qualification
    if (value) {
      this.qualifications.push(value);
    }

    // Clear the input value
    event.chipInput!.clear();
  }

  editQualification(qualification: string, event: MatChipEditedEvent) {
    const value = event.value.trim();

    // Remove qualification if it no longer has a name
    if (!value) {
      this.removeQualification(qualification);
      return;
    }

    // Edit existing qualification
    const index = this.qualifications.indexOf(qualification);
    if (index >= 0) {
      this.qualifications[index] = value;
    }
  }

  removeLicence(licence: string) {
    const index = this.licences.indexOf(licence);
    if (index >= 0) {
      this.licences.splice(index, 1);

      this.announcer.announce(`removed ${licence}`);
    }
  }

  addLicence(event: MatChipInputEvent): void {
    const value = (event.value || '').trim();
    console.log(event);

    // Add our licence
    if (value) {
      this.licences.push(value);
    }

    // Clear the input value
    event.chipInput!.clear();
  }

  editLicence(licence: string, event: MatChipEditedEvent) {
    const value = event.value.trim();

    // Remove licence if it no longer has a name
    if (!value) {
      this.removeQualification(licence);
      return;
    }

    // Edit existing licence
    const index = this.licences.indexOf(licence);
    if (index >= 0) {
      this.licences[index] = value;
    }
  }

  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();
  }
}
