import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { Exercise } from '../models/exercise';
import { environment } from '@env/environment';

@Injectable({
  providedIn: 'root',
})
export class SearchService {
  private http = inject(HttpClient);

  apiURLSearch = environment.apiURL + 'search';

  searchExercises(
    searchTerm: string,
    filters: any,
    langCode: string,
  ): Observable<Exercise[]> {
    const requstBody = {
      searchTerm: searchTerm,
      filters: filters,
    };

    const params = {
      langCode: langCode,
    };

    return this.http.post<Exercise[]>(this.apiURLSearch, requstBody, {
      params,
    });
  }
}
