  ngOnInit(): void {
    this.selectedLanguage = this._getUserLanguageToken();
    console.log('running from search');
    const subscription = combineLatest([
      // concat(of(''), this.searchTerm.valueChanges),
      this.searchTerm.valueChanges.pipe(
        startWith(''),
        tap(() => this.isFetchingData.set(true)),
      ),
      this.shareDataService.filterData$,
    ])
      .pipe(
        debounceTime(400),
        distinctUntilChanged(),
        switchMap(([searchTerm, filterData]) =>
          this.searchService.searchExercises(
            searchTerm,
            filterData,
            this.selectedLanguage,
          ),
        ),
      )
      .subscribe(searchResult =>
        this.shareDataService.updateSearchResults(searchResult),
      );
    this.subscriptions.add(subscription);
  }

  clearSearch() {
    this.searchTerm.setValue('');
  }

