import { HttpClient } from '@angular/common/http';
import { inject, Injectable } from '@angular/core';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';

interface Country {
  id: string;
  name: string;
}

@Injectable({
  providedIn: 'root',
})
export class CountriesLibService {
  private http = inject(HttpClient);

  apiURLCountriesLib = environment.apiURL + 'countriesLib';

  getCountriesLib(lang: string): Observable<Country[]> {
    return this.http.get<Country[]>(`${this.apiURLCountriesLib}/${lang}`);
  }
}
