import {
  AfterViewInit,
  Component,
  inject,
  Input,
  OnDestroy,
  OnInit,
  QueryList,
  ViewChildren,
} from '@angular/core';
import { Exercise } from '../../models/exercise';
import {
  MatExpansionPanel,
  MatAccordion,
  MatExpansionPanelHeader,
  MatExpansionPanelTitle,
  MatExpansionPanelDescription,
} from '@angular/material/expansion';
import {
  CdkDragDrop,
  moveItemInArray,
  CdkDropList,
  CdkDrag,
  CdkDragPlaceholder,
  CdkDragHandle,
} from '@angular/cdk/drag-drop';
import { SelectedExercisesService } from '../../services/selected-exercises.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable, Subscription } from 'rxjs';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { ThemeManagerService } from '@slowautomobile/ui';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { FormsModule } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatFormField, MatLabel } from '@angular/material/form-field';
import { MatTooltip } from '@angular/material/tooltip';
import { MatIconButton } from '@angular/material/button';
import { MatIcon } from '@angular/material/icon';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';
import { MatDialog } from '@angular/material/dialog';
import { ExercisesService } from '../../services/exercises.service';
import { ExerciseModalComponent } from '../exercise-modal/exercise-modal.component';
import { LocalstorageService } from '@slowautomobile/users';

@Component({
  selector: 'exercises-selected-expansion-panel',
  templateUrl: './selected-expansion-panel.component.html',
  styleUrl: './selected-expansion-panel.component.scss',
  imports: [
    OverlayscrollbarsModule,
    CdkDropList,
    MatAccordion,
    MatExpansionPanel,
    CdkDrag,
    CdkDragPlaceholder,
    MatExpansionPanelHeader,
    CdkDragHandle,
    MatIcon,
    MatExpansionPanelTitle,
    MatExpansionPanelDescription,
    MatIconButton,
    MatTooltip,
    MatFormField,
    MatLabel,
    MatInput,
    FormsModule,
    CdkTextareaAutosize,
  ],
})
export class SelectedExpansionPanelComponent implements OnInit, OnDestroy {
  private selectedExercisesService = inject(SelectedExercisesService);
  private _snackBar = inject(MatSnackBar);
  dialog = inject(MatDialog);
  private exercisesService = inject(ExercisesService);
  private localstorageService = inject(LocalstorageService);

  @Input() exercise!: Exercise;
  // @Input() programName!: string;
  previewClass = 'expansion-panel-custom-preview';

  @ViewChildren(MatExpansionPanel) panels!: QueryList<MatExpansionPanel>;
  selectedExercises = this.selectedExercisesService.selectedExercises;
  removedExercise?: Exercise | null;
  isExpanded = false;
  subscriptions = new Subscription();
  selectedLanguage!: 'en' | 'de' | 'sr';

  themeManager = inject(ThemeManagerService);
  themeSignal = this.themeManager.themeSignal;

  ngOnInit(): void {
    this.selectedLanguage = this._getUserLanguageToken();
  }

  togglePanel(index: number) {
    // Use the index to get the specific panel reference from the array and toggle its state
    const panelToToggle = this.panels.toArray()[index];

    if (panelToToggle) {
      panelToToggle.toggle();
    }
  }

  private _getExercise(exerciseId: string): Observable<Exercise> {
    return this.exercisesService.getExercise(exerciseId);
  }

  showExerciseModal(
    exerciseId: string,
    exerciseCard: Exercise,
    isProgramExercise: boolean,
  ): void {
    const subscription = this._getExercise(exerciseId).subscribe(exercise => {
      this.dialog.open(ExerciseModalComponent, {
        data: {
          fullExercise: exercise,
          exerciseCard: exerciseCard,
          isProgramExercise: isProgramExercise,
          selectedLang: this.selectedLanguage,
        },
        width: '500px',
        // height: '736px',
        panelClass: ['custom-dialog', isProgramExercise ? 'custom-shadow' : ''],
        // disableClose: isProgramExercise,
        // hasBackdrop: isProgramExercise ? false : true,
        backdropClass: isProgramExercise ? 'custom-backdrop' : '',
        // enterAnimationDuration: 0,
      });
    });
    this.subscriptions.add(subscription);
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(
      this.selectedExercises(),
      event.previousIndex,
      event.currentIndex,
    );
  }

  // Remove and undo with message snackbar
  removeFromSelected(exercise: Exercise) {
    this.selectedExercisesService.removeFromSelected(exercise);
    this.removedExercise = exercise;

    // Can't set custom style correctly; wait for m3 ng material, until then snackBar component or CHANGE COMPLETE MATERIAL THEME!!!
    const snackBarRef = this._snackBar.open(
      $localize`:@@Snackbar.exerciseDeleted:Exercise deleted`,
      $localize`:@@Snackbar.undoButton:Undo`,
      {
        duration: 4000,
        panelClass: ['custom-snackbar-style'],
      },
    );

    // Listen for the Undo action
    const subscription = snackBarRef
      .onAction()
      .subscribe(() => this.undoRemove());
    this.subscriptions.add(subscription);

    console.log(this.selectedExercisesService.selectedExercises());
  }

  undoRemove(): void {
    if (this.removedExercise) {
      // Add the removed item back to its original position
      // this.items.splice(this.removedItem.index, 0, this.removedItem.value);
      this.selectedExercisesService.addToSelected(this.removedExercise);

      // Reset the removedItem reference
      this.removedExercise = null;
    }
  }

  private _getUserLanguageToken(): 'en' | 'de' | 'sr' {
    return <'en' | 'de' | 'sr'>(
      this.localstorageService.getToken('userLanguage')?.split('-')[0]
    );
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
